//layout mixins - start

@mixin qodefTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin qodefTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin qodefRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin qodefAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin qodefTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

//layout mixins - end

//transition mixins - start

@mixin qodefTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin qodefTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin qodefTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin qodefAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin qodefTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

//transition mixins - end

//checkbox mixins - start

$checkbox-size: 20px;
$checkbox-border-width: 1px;
%checkbox-style {
    position: relative;
    margin: 14px 0;
    line-height: 1;
    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;

        &:focus {
            outline: 0;
        }
    }
    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }
    input[type=checkbox] + label span.qodef-label-text {
        padding-left: 10px;
        display: inline-block;
        font-size: 14px;
        line-height: $checkbox-size;
        color: $default-text-color;
        text-transform: capitalize;
        font-weight: 400;
        font-family: $default-text-font;
    }
    input[type=checkbox] + label .qodef-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid #e1e1e1;
        background-color: $default-background-color;
        &:hover {
            cursor: pointer;
        }
    }
    input[type=checkbox] + label .qodef-label-view:after {
        content: "\4e";
        font-size: 12px;
        font-family: 'ElegantIcons';
        color: #fff;
        position: absolute;
        top: 10px;
        left: 5px;
        opacity: 0;
        @include qodefTransition(opacity 0.3s ease-in-out);
    }
    input[type=checkbox]:checked {

        + label .qodef-label-view {
            background-color: $first-main-color;
            border-color: $first-main-color;

            &:after {
                opacity: 1;
            }
        }
    }
}

//checkbox mixins - end

//overrides theme styled owl nav
%basic-nav {
    display: block;
    position: static;
    background-color: transparent;
    padding: 0;
    color: currentColor;

    .qodef-nav-label {
        display: none;
    }
}

/* common mixins - start */

@mixin qodefBckImageStyle(){
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin qodefImageOverlayHoverStyle($with-hover: true){

    @if ($with-hover) {

        &:hover {

            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include qodefAbsoluteHolderLayout();
            content: '';
            background-color: rgba($first-main-color, .25);
            opacity: 0;
            @include qodefTransition(opacity .2s);
        }

    } @else {
        @include qodefAbsoluteHolderLayout();
        content: '';
        background-color: rgba($first-main-color, .25);
        opacity: 0;
        @include qodefTransition(opacity .2s);
    }
}

@mixin qodefButtonDefaultStyle() {
    font-family: $default-heading-font;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    outline: none;
    font-size: 14px;
    line-height: 2em;
    font-weight: 700;
    text-transform: uppercase;
    box-sizing: border-box;
    margin: 0;
    @include qodefTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin qodefButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin qodefButtonSolidColor() {
    color: #fff;
    background-color: transparent;
    border: 2px solid $default-heading-color;
}

@mixin qodefButtonSolidHoverColor() {
    color: $first-main-color;
    background-color: transparent;
    border: 2px solid $first-main-color;
}

@mixin qodefButtonOutlineColor() {
    color: $first-main-color;
    background-color: transparent;
    border: 2px solid $first-main-color;
}

@mixin qodefButtonOutlineHoverColor() {
    color: #fff;
    background-color: $first-main-color;
    border-color: $first-main-color;
}

@mixin qodefButtonSmallParams() {
    padding: 8px 44px;
}

@mixin qodefButtonMediumParams() {
    padding: 10px 53px;
}

@mixin qodefButtonLargeParams() {
    padding: 13px 62px;
}

@mixin qodefButtonHugeParams() {
    display: block;
    text-align: center;
    padding: 13px 26px;
}

@mixin qodefPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }
    &:-moz-placeholder {
        @content;
        opacity: 1;
    }
    &::-moz-placeholder {
        @content;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

/* common mixins - end */

/* mixins styles - start */

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 24px;
    padding: 5px 0;
    font-family: $default-heading-font;
    font-weight: inherit;
    font-size: 17px;
    line-height: 20px;
    color: $default-heading-color;
    background-color: transparent;
    border: 0;
    border-bottom: 2px solid $default-heading-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    cursor: pointer;
    box-sizing: border-box;
    @include qodefTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include qodefPlaceholder {
        color: inherit;
        text-transform: uppercase;
        font-weight: 700;
    }
}

/* mixins styles - end */

